    score: function (){
      let base_score = 60.0;
      let nece_score = 20.0;
      let add_score = (Math.random()*(10-1) + 1).toFixed(2);
      let infor_score = (Math.random()*(10-1) + 1).toFixed(2);
      if (this.ruleList_necessary.length === 0) nece_score = 0;
      for (let i=0; i<this.ruleList_necessary.length; i++){
        if (this.ruleList_necessary[i].result === '不符合'){
          nece_score = 0;
          break;
        }
      }

      this.com_credit_score[0].value = base_score;
      this.com_credit_score[1].value = nece_score;
      this.com_credit_score[2].value = add_score;
      this.com_credit_score[3].value = infor_score;
      console.log(add_score)
      console.log(infor_score)
      this.com_credit_score[4].value = (base_score + nece_score + Number(add_score) + Number(infor_score)).toFixed(2);
